import React from 'react';

const Card = ({title,price}) => {
    return (
        <div className="card">
            <div className="card__header">
                <div className="card__title">{ title }</div>
            </div>
            <div className="card__body">
                <div className="card__price">$ { price }</div>
                <div className="card__description">Per month</div>
            </div>
            <div className="card__action">
                <div className="card__line__0 line"></div>
                <div className="card__line__1 line"></div>
                <div className="card__line__2 line"></div>
                <div className="card__line__3 line"></div>
            </div>
        </div>
    )
}

export default Card;
